/* Main container */
.edit-categories-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
  }
  
  /* Header container */
  .header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 20px;
  }
  
  .title {
    font-size: 18px;
    font-weight: bold;
    color: #333;
    text-align: center;
    flex: 1;
  }
  
  /* Back arrow */
  .back-arrow {
    font-size: 24px;
    cursor: pointer;
    color: #888;
  }
  
  .back-arrow:hover {
    color: #555;
  }
  
  /* Add category icon with matte green color and rounded corners (27%) */
  .add-category-icon {
    font-size: 24px;
    color: #4CAF50;
    padding: 10px;
    border-radius: 27%;
    background-color: rgba(76, 175, 80, 0.2);
    cursor: pointer;
  }
  
  .add-category-icon:hover {
    background-color: rgba(76, 175, 80, 0.3);
  }
  
  /* Table styling */
  .categories-table {
    width: 90%;
    border-collapse: collapse;
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    margin-bottom: 40px;
  }
  
  .categories-table th,
  .categories-table td {
    padding: 12px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }
  
  .categories-table th {
    background-color: #f2f2f2;
  }
  
  .categories-table td:hover {
    background-color: #f9f9f9;
    cursor: pointer;
  }
  
  /* Modal styling */
  .modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .modal-content {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    width: 400px;
    max-width: 90vw;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .modal-header {
    display: flex;
    justify-content: flex-start;
  }
  
  .trashcan-icon {
    cursor: pointer;
    font-size: 18px;
    color: #888;
    margin-bottom: 10px;
  }
  
  .trashcan-icon:hover {
    color: #ff0000;
  }
  
  /* Modern buttons for modal */
  .modal-buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  }
  
  .save-button {
    background-color: #4CAF50;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .save-button:hover {
    background-color: #45a049;
  }
  
  .cancel-button {
    background-color: #ccc;
    color: black;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .cancel-button:hover {
    background-color: #bbb;
  }
  
  /* Message bubble */
  .message-bubble {
    position: fixed;
    bottom: 60px; /* Positioned above the nav bar */
    left: 50%;
    transform: translateX(-50%);
    background-color: #4CAF50;
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 14px;
    z-index: 1000;
    opacity: 1;
    transition: opacity 0.5s ease-in-out;
  }
  
  /* Error message bubble */
  .message-bubble.error {
    background-color: #f44336; /* Matte light red for error */
  }
  