/* Container for the Add Cost form */
.add-cost-container {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
}

/* Heading for the Add Cost form */
h2 {
  text-align: center;
  font-size: 24px;
  margin-bottom: 20px;
}

/* Form group styling */
.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  font-size: 14px;
  margin-bottom: 5px;
  color: #333;
}

/* Inputs styling */
input[type="date"],
input[type="text"],
input[type="number"],
select {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #999;
  border-radius: 5px;
  box-sizing: border-box;
}

input:focus {
  border-color: #999;
  outline: none;
}

/* Button group for assigned cost (Split/User1/User2) */
.button-group-container {
  display: flex;
  justify-content: space-between;
}

.button-group-container button {
  flex: 1;
  padding: 10px;
  margin-right: 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  background-color: #f2f2f2;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}

.button-group-container button:last-child {
  margin-right: 0;
}

.button-group-container button.active {
  background-color: #007bff;
  color: white;
  border-color: #007bff;
}

.button-group-container button:hover {
  background-color: #ddd;
}

/* Submit button styling */
.submit-btn {
  width: 100%;
  padding: 12px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.submit-btn:hover {
  background-color: #0056b3;
}

/* Message styling */
p {
  text-align: center;
  margin-top: 20px;
  color: green;
}

/* Recent Costs Section */
.recent-costs-container {
  margin-top: 30px;
}

.recent-costs-container h3 {
  font-size: 18px;
  margin-bottom: 10px;
  text-align: left;
}

/* Styling the list of recent costs */
.recent-costs-list {
  margin-top: 10px;
  padding: 0;
  list-style-type: none;
  margin-bottom: 55px; /* Adds space at the bottom to avoid overlap */
}

.recent-costs-list li {
  margin-top: 5px;
  display: flex;
  justify-content: space-between;
  padding: 0px;
  border-bottom: 1px solid #ddd;
  font-size: 14px;
  line-height: 1.4;
}

.cost-info {
  display: flex;
  flex-direction: column; /* Ensures the description and amount are stacked */
}

.cost-description {
  font-weight: bold;
  font-size: 14px;
}

.cost-amount {
  color: #007bff;
  font-size: 14px;
}

.cost-details {
  display: flex;
  flex-direction: column; /* Ensures the details (category, assign to, and date) are stacked */
  text-align: right; /* Aligns the right column */
}

.cost-category {
  font-style: italic;
  font-size: 12px;
  color: #555;
}

.cost-assigned {
  color: #292929;
  font-size: 12px;
}

.cost-date {
  font-size: 12px;
  color: #999;
  margin-bottom: 5px;
}

/* Modal Styles */
/* AddCost Modal - Full CSS */

/* Modal backdrop that covers the entire screen */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw; /* Full width of the viewport */
  height: 100vh; /* Full height of the viewport */
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
  display: flex;
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
  z-index: 1000; /* Ensure it's above all other content */
}

/* Modal content box */
.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  width: 400px;
  max-width: 90vw; /* For smaller screens */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Add a subtle shadow */
  max-height: 80vh; /* Prevents overflow on smaller screens */
  overflow-y: auto; /* Allow scrolling if content is too long */
}

/* Adjustments for the buttons */
.modal-buttons {
  display: flex;
  justify-content: space-between; /* Creates space between buttons */
  margin-top: 20px;
}

.save-btn,
.cancel-btn {
  width: 48%; /* Each button takes 48% of the width for better space management */
  padding: 10px;
  font-size: 16px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  text-align: center;
}

.save-btn {
  background-color: #4caf50;
  color: white;
}

.save-btn:hover {
  background-color: #45a049;
}

.cancel-btn {
  background-color: #d3d3d3;
  color: black;
}

.cancel-btn:hover {
  background-color: #a9a9a9;
}
