.budget-page-container {
    padding: 20px;
    max-width: 1000px;
    margin: 0 auto;
  }
  
  h2 {
    text-align: center;
  }
  
  .add-budget-btn {
    padding: 10px 20px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    display: block;
    margin: 20px auto;
    font-size: 16px;
  }
  
  .add-budget-btn:hover {
    background-color: #45a049;
  }
  
  .budget-cards-container {
    margin-top: 30px;
    text-align: center;
    color: #888;
  }
  