.new-budget-page-container {
    padding: 20px;
    max-width: 1000px;
    margin: 0 auto;
    text-align: center;
  }
  
  .new-budget-title {
    font-size: 24px;
    font-weight: bold;
    color: #333;
    margin-top: 20px;
    text-align: center;
  }
  
  .back-button-container {
    position: absolute;
    top: 20px;
    left: 20px;
    cursor: pointer;
  }
  
  .back-button-icon {
    font-size: 20px;
    color: #555;
  }
  
  .back-button-icon:hover {
    color: #000;
  }
  
  /* Form styling */
  .budget-form {
    margin-top: 40px;
    text-align: left;
  }
  
  .budget-form label {
    font-size: 16px;
    color: #333;
    width: 150px;  /* Ensures all labels have the same width */
    display: inline-block;  /* Aligns the labels inline */
  }
  
  .budget-form input[type="month"],
  .budget-form input[type="number"] {
    width: 100px; /* Smaller input width */
    padding: 5px;
    font-size: 14px;
    margin-left: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    display: inline-block;
  }
  
  .input-row {
    margin-bottom: 10px;
  }
  
  /* Income Section */
  .income-section {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .income-title {
    font-size: 20px;
    font-weight: bold;
    color: #333;
    margin-right: 10px;
  }
  
  .total-income {
    font-size: 16px;
    font-weight: bold;
    color: #4caf50;
    margin-left: 10px;
  }
  