.settings-container {
    display: flex;
    flex-direction: column;
    padding: 20px;
    min-height: 100vh;
    box-sizing: border-box;
}

.settings-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 100%;
    align-items: flex-start; /* Align buttons and header to the left */
}

h1 {
    font-size: 15px;
    font-weight: lighter;
    margin-bottom: 10px;
    text-align: left; /* Align header to the left */
}

.logout-btn, .edit-categories-btn {
    width: 200px;
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    display: block;
    text-align: center;
}

.edit-categories-btn {
    background-color: #d3d3d3; /* Gray button for Edit Categories */
    color: black;
}

.edit-categories-btn:hover {
    background-color: #a9a9a9; /* Darker gray on hover */
}

.logout-btn {
    background-color: #ff4d4d;
}

.logout-btn:hover {
    background-color: #ff3333;
}
