/* App.css */
body, html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
}

.container {
  padding: 20px;
  margin-top: 60px; /* Adjust based on the height of your top bar */
}

.top-navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #333;
  color: white;
  padding: 10px;
  text-align: left;
  z-index: 9999; /* Ensure it stays above other content */
}

.bottom-navbar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #333;
  color: white;
  height: 60px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  z-index: 9999;
}

.bottom-navbar .icon-container {
  text-align: center;
  flex-grow: 1;
  color: white;
}

.bottom-navbar .icon-container.active {
  color: #42b72a; /* Highlight color */
}

.bottom-navbar .icon {
  font-size: 24px; /* Larger size for the icons */
}

.bottom-navbar .text {
  font-size: 10px; /* Subtle size for the text */
}
