.bottom-nav {
  display: flex;
  justify-content: space-around;
  background-color: #f7f7f7;
  padding: 10px 0;
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  border-top: 1px solid #ccc;
  z-index: 100;
}

.nav-item {
  text-align: center;
  flex-grow: 1;
  color: #888;
  text-decoration: none;
}

.nav-item.active {
  color: #007bff;
}

.nav-item i {
  display: block;
  font-size: 24px; /* Larger icon */
  margin-bottom: 5px;
}

.nav-item span {
  font-size: 12px; /* Smaller, subtle text */
  display: block;
}
