/* Main container for costs */
.costs-container {
    padding: 20px;
    margin: 0 auto;
    max-width: 1000px;
  }
  
  /* Search bar styling */
  .search-bar {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .search-bar input {
    width: 70%; /* Adjusted width for the search bar */
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 16px;
  }
  
  /* Filter dropdown styling */
  .filter-dropdown {
    width: 30%;
    padding: 10px;
    margin-left: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 16px;
  }
  
  /* List styling */
  .costs-list-container {
    margin: 20px 0;
  }
  
  .costs-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
    margin-bottom: 80px;
  }
  
  .costs-list li {
    display: flex;
    justify-content: space-between;
    padding: 15px;
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 8px;
    margin-bottom: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .costs-list li:hover {
    background-color: #e9e9e9;
  }
  
  .cost-info {
    display: flex;
    flex-direction: column;
  }
  
  .cost-details {
    display: flex;
    align-items: center;
  }
  
  .cost-description {
    font-weight: bold;
  }

  .cost-amount {
    font-weight: bold;
    color: #4a9229;
  }
  
  .cost-category,
  .cost-date,
  .cost-assigned {
    margin-left: 10px;
    font-size: 14px;
    color: #555;
  }
  
  /* Modal styling */
  .modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .modal-content {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    width: 400px;
    max-width: 90vw;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .modal-header {
    display: flex;
    justify-content: flex-start;
  }
  
  .trashcan-icon {
    cursor: pointer;
    font-size: 18px;
    color: #888;
    margin-bottom: 10px;
  }
  
  .trashcan-icon:hover {
    color: #ff0000;
  }
  
  /* Button group */
  .button-group-container {
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
  }
  
  .button-group-container button {
    padding: 10px;
    flex: 1;
    margin: 0 5px;
    border-radius: 5px;
    border: none;
    background-color: #ddd;
    cursor: pointer;
  }
  
  .button-group-container button.active {
    background-color: #4caf50;
    color: white;
  }
  
  /* Save and Cancel buttons */
  .save-btn,
  .cancel-btn {
    padding: 10px;
    margin-top: 20px;
    width: 100%;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .save-btn {
    background-color: #4caf50;
    color: white;
  }
  
  .save-btn:hover {
    background-color: #45a049;
  }
  
  .cancel-btn {
    background-color: #ccc;
    color: black;
  }
  
  .cancel-btn:hover {
    background-color: #bbb;
  }
  
  /* Confirmation pop-up for deletion */
  .confirmation-pop-up {
    padding: 20px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .confirmation-pop-up p {
    margin-bottom: 20px;
  }
  
  .confirm-delete-btn,
  .cancel-delete-btn {
    padding: 10px;
    margin-top: 10px;
    width: 100%;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .confirm-delete-btn {
    background-color: #f44336;
    color: white;
  }
  
  .confirm-delete-btn:hover {
    background-color: #e41e26;
  }
  
  .cancel-delete-btn {
    background-color: #ccc;
  }
  
  .cancel-delete-btn:hover {
    background-color: #bbb;
  }
  